<template>
    <div style="margin-top: 40px">
        <el-form :inline="true" :model="queryParam" class="demo-form-inline">
            <el-form-item label="用户账号">
                <el-input v-model="queryParam.code" placeholder="用户账号" clearable></el-input>
            </el-form-item>
            <el-form-item label="用户名称">
                <el-input v-model="queryParam.name" placeholder="用户名称" clearable></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>

        <el-button type="primary" @click="create">创建</el-button>

        <el-table
                :data="listData"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="code"
                    label="用户账号"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="用户名称"
                    width="180"
            >
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label="电话号码"
            >
            </el-table-column>
            <el-table-column
                    prop="email"
                    label="邮箱">
            </el-table-column>
            <el-table-column
                    prop="idCard"
                    label="身份证号"
            >
            </el-table-column>
<!--            <el-table-column-->
<!--                    prop="isManager"-->
<!--                    label="是否管理员"-->
<!--            >-->
<!--            </el-table-column>-->
            <el-table-column
                    prop="description"
                    label="描述"
            >
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="text"  @click="detail(scope.row)">详情</el-button>
                    <el-button type="text" @click="update(scope.row)">修改</el-button>
                    <el-button type="text" @click="doDelete(scope.row)">删除</el-button>
                </template>

            </el-table-column>
        </el-table>
        <el-row :gutter="20" style="margin-top: 25px">
            <el-col :span="4" :offset="18">
                <el-pagination
                        background
                        :layout=pageInfo.layout
                        :total="pageInfo.total">
                </el-pagination>
            </el-col>
        </el-row>


        <el-dialog
                :title="title"
                :visible.sync="dialogVisible"
                width="40%">
            <el-form ref="form"  :model="formData" label-width="200px" size="medium">
                <el-form-item label="用户账号" prop="code" :disabled="!isDetail">
                    <el-input v-model="formData.code" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="用户名称" prop="name" :disabled="!isDetail">
                    <el-input v-model="formData.name"  style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password" :disabled="!isDetail">
                    <el-input type="password" v-model="formData.password" autocomplete="off" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item v-if="!isDetail" label="确认密码" prop="checkPassword" :disabled="!isDetail">
                    <el-input type="password" v-model="formData.checkPassword" autocomplete="off" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="电话号码" prop="phone" :disabled="!isDetail">
                    <el-input v-model="formData.phone"  style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item  label="邮箱" prop="email" :disabled="!isDetail">
                    <el-input v-model="formData.email"  style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item  label="身份证号" prop="idCard" :disabled="!isDetail">
                    <el-input v-model="formData.idCard" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item  label="描述" prop="description" :disabled="!isDetail">
                    <el-input v-model="formData.description" type="textarea"   rows="3" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item v-if="isDetail">
                    <el-button @click="cancel">取消</el-button>
                </el-form-item>
                <el-form-item v-if="!isDetail">
                    <el-button type="primary" v-if="formData.id" @click="doUpdate">保存</el-button>
                    <el-button type="primary" v-else @click="doCreate">立即创建</el-button>
                    <el-button @click="cancel">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
    import {listUser,deleteUser,updateUser,createUser} from "../../../api/userApi";

    export default {
        name: "list",
        data() {
            return {

                listData: [],
                queryParam: {
                    "pageNumber": 1,
                    "pageSize": 10,
                    "name": null,
                    "code": null

                },
                dialogVisible: false,
                formData: {
                    id: null,
                    code: null,
                    name: null,
                    password: null,
                    checkPassword: null,
                    phone: null,
                    email: null,
                    idCard: null,
                    isManager: null,
                    description: null
                },
                managerList: [
                    {
                        "name": "SmsAdmin",
                        "code": "SmsAdmin"
                    }
                ],
                pageInfo: {
                    layout: "prev, pager, next, jumper, total",
                    total: 10
                },
                isDetail: false,
                title: "创建用户"
            }
        },
        created() {
            this.onSubmit()
        },
        methods: {
            listUsers() {
                listUser(this.queryParam).then(res => {
                    this.listData = res.data.first
                    this.pageInfo.total = res.data.second
                })
            },
            create() {
                this.isDetail = false
                this.dialogVisible = true
                this.initFormData()
            },
            doCreate() {
                createUser(this.formData).then(res => {
                    this.dialogVisible = false
                    this.initFormData()
                    this.$message({
                        message: '创建成功',
                        type: 'success'
                    });
                    this.listUsers()
                })
            },
            onSubmit() {
                this.listUsers()
            },
            initFormData(row) {
                if (row != null) {
                    this.formData = {
                        id: row.id,
                        code: row.code,
                        name: row.name,
                        password: row.password,
                        phone: row.phone,
                        email: row.email,
                        idCard: row.idCard,
                        isManager: row.isManager,
                        description: row.description
                    }
                } else {
                    this.formData = {
                        id: null,
                        code: null,
                        name: null,
                        password: null,
                        phone: null,
                        email: null,
                        idCard: null,
                        isManager: null,
                        description: null
                    }
                }

            },
            update(row) {
                this.title = "编辑用户"
                this.isDetail = false
                this.initFormData(row);
                this.dialogVisible = true
            },
            doUpdate() {
                updateUser(this.formData).then(res => {
                    this.dialogVisible = false
                    this.initFormData()
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                    this.listUsers()
                }).catch(error =>{
                    this.dialogVisible = false
                    this.initFormData()
                    this.$message({
                        message: '修改失败',
                        type: 'error'
                    });
                    this.listUsers()
                } )
            },
            doDelete(row) {
                const params = []
                params.push(row.id)
                deleteUser(params).then(res => {
                    this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                    this.listUsers()
                })
            },
            detail(row) {
                this.title ="用户详情"
                this.initFormData(row);
                this.isDetail = true
                this.dialogVisible = true
            },
            cancel() {
                this.dialogVisible = false
                this.isDetail = false
            }
        }
    }
</script>

<style scoped>

</style>